<template>
  <div class="page">
    <div style="min-height: 100vh">
      <div class="page_title">
        <span class="left">
          <van-icon name="arrow-left" size="25" @click="$router.go(-1)" />
        </span>
        <span>
          <img src="../../assets/image/hotel.png" alt="" />
        </span>
        <span>
          酒店住宿
          <br />
          Hotel
        </span>
      </div>
      <div class="page_cont">
        <div class="routertype">
          <div>
            <div class="search-cont">
              <div class="cont">
                <div style="padding: 0 20px">
                  <van-col span="10">
                    <span>目的地/Destination</span>
                    <van-field readonly clickable v-model="hotelDestination" placeholder="选择城市" @click="showPicker = true; get_district_list();" />
                  </van-col>
                </div>
              </div>
              <div class="cont" style="padding: 0 20px">
                <van-col span="10">
                  <span>入住日期/Check-In</span>
                  <van-field readonly clickable v-model="date_D[0]" placeholder="选择日期" @click="
                      showPickerTwo = true;
                      date_n = 0;
                    " />
                </van-col>
                <van-col span="4"></van-col>
                <van-col span="14" class="aliginright">
                  <span>退房日期/Check-out</span>
                  <van-field readonly clickable v-model="date_D[1]" placeholder="选择日期" @click="
                      showPickerTwo = true;
                      date_n = 1;
                    " />
                </van-col>
              </div>
              <div class="cont">
                <div style="padding: 0 20px" class="holeClass">
                  <van-col span="12">
                    <span>酒店级别/Hotel Level</span>
                    <van-field readonly clickable v-model="cabin" placeholder="选择座位等级" @click="showPicker3 = true" />
                  </van-col>
                  <van-col span="12" class="aliginright">
                    <span>区域位置/Location</span>
                    <van-field readonly clickable v-model="hotelRegionalPosition" placeholder="请选择/choose" @click="showPicker5 = true;get_district_list();" />
                  </van-col>
                </div>
              </div>
              <div class="cont">
                <div style="padding: 0 20px" class="hotelKeyword">
                  <van-col span="24">
                    <span>关键词/Keywords</span>
                    <van-field v-model="keyword" placeholder="请输入关键词/Please enter key words" />
                  </van-col>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="travrler">
        <psd_cont></psd_cont>
      </div>
    </div>
    <div class="next" @click="searchb">
      <span>下一步<br />Next Step</span>
    </div>
    <div class="pup">
      <van-popup class="select_popup van_listp" v-model:show="showPicker" round position="bottom" :style="{ height: '40%' }">
        <van-search v-model="searchValuex" placeholder="请输入城市名" />
        <!-- <infinite :listData="search_citylist" :list_value="'CityNameChinese'" :list_valueEn="'CityNameEnglish'" :screenHeight="400"></infinite> -->
        <van-pull-refresh v-model="hotelRefreshing" @refresh="hotelOnRefresh">
          <div v-for="(item, index) in search_citylist" :key="index">
            <span @click="hotelSelectCity(item)">{{ item.CityNameChinese + '(' + item.CityNameEnglish + ')'}}</span>
            <!-- 其他数据展示 -->
          </div>
        </van-pull-refresh>
      </van-popup>
      <van-calendar v-model:show="showPickerTwo" type="range" @confirm="onConfirmTwo" color="#044db5" title="日期选择/Date selection" confirm-text="完成/Confirm" confirm-disabled-text="请选择结束时间/Select end time" :formatter="formatter" />
      <van-popup class="select_popup" v-model:show="showPicker3" round position="bottom">
        <van-picker show-toolbar allow-html :columns="cabins" @confirm="onConfirm3" @cancel="showPicker3 = false" />
      </van-popup>
      <van-popup class="select_popup" v-model:show="showPicker5" round position="bottom">
        <van-search v-model="search_district" placeholder="请输入地区名" />
        <van-picker show-toolbar value-key="LocationName" allow-html :columns="filter_district_list" @confirm="onConfirm5" @cancel="showPicker5 = false" />
      </van-popup>
    </div>
  </div>
</template>
<script>
import psd_cont from "../psgcontPage/psgCont.vue";
import infinite from "../children/Trainticket.vue";
import axios from '../../utils/axiosLogin.js'
import { showToast } from 'vant';
export default {
  data () {
    return {
      showPicker: false,
      showPicker2: false,
      showPickerTwo: false,
      showPicker3: false,
      showPicker5: false,
      cities: [
        {
          LocIdCity: "0201",
          CityNameChinese: "上海 ",
          CityNameEnglish: "SHANGHAI ",
        },
        {
          CityNameChinese: "北京",
          CityNameEnglish: "BEIJING",
          LocIdCity: "867219481552416984",
          city_code: "BEIJING_BJ",
          initial: "B",
          pro_code: "0100",
        },
      ],
      cities_n: 0,
      date_D: [
        new Date().format("yyyy-MM-dd"),
        new Date(new Date().getTime() + 24 * 3600 * 1000).format("yyyy-MM-dd"),
      ],
      date_picker: [
        new Date(),
        new Date(new Date().getTime() + 24 * 3600 * 1000 * 3650),
        new Date(),
      ],
      cabins: [
        { value: "-1", text: "不限/NoLimit" },
        { value: "0,1,2", text: "经济/客栈/≤2*" },
        { value: "3", text: "三星/舒适/3" },
        { value: "4", text: "四星/高档/4" },
        { value: "5", text: "五星/豪华/5" },
      ],
      // 目的地选择
      hotelRefreshing: false,
      level: null,
      cabin: "不限/NoLimit",
      district: null,
      hotelRegionalPosition: '',
      keyword: "",
      citylist: [],
      district_list: [],
      searchValuex: "",
      search_district: "",
      now_trip_hotel: [
        {
          city: {
            $id: "121",
            LocIdCity: "0201",
            CityNameEnglish: "Shanghai",
            CityNameChinese: "上海 ",
          },
          checkout: this.dest_arr
            ? this.dest_arr.checkout
            : new Date(new Date().getTime() + 24 * 3600 * 1000).format(
              "yyyy-MM-dd"
            ),
          checkin: this.dest_arr
            ? this.dest_arr.checkin
            : new Date().format("yyyy-MM-dd"),
        },
      ],
      dest_arr: sessionStorage.getItem("dest_arr")
        ? JSON.parse(sessionStorage.getItem("dest_arr"))
        : null,
    };
  },
  created () {
    if (sessionStorage.getItem("search_hotel")) {
      sessionStorage.removeItem("search_hotel");
    }
    if (sessionStorage.getItem("search_hotelx")) {
      let g = JSON.parse(sessionStorage.getItem("search_hotelx"));
      // this.$store.state.choose_psg = g.psg;
      this.cabin = g.cabin;
      this.cities = g.cities;
      this.date_D = g.date;

      this.district = g.district;
    }
    if (this.dest_arr) {
      this.now_trip_hotel[0].city = {
        CityNameEnglish: " ",
        CityNameChinese: this.dest_arr.city,
      };
    }
    this.set_address_for_hotel_getcity();
    this.get_district_list();
  },
  computed: {
    search_citylist () {
      if (!this.searchValuex) {
        return this.citylist;
      }
      return this.citylist.filter((v) => {
        return (
          v.CityNameChinese.indexOf(this.searchValuex) != -1 ||
          v.CityNameEnglish.toLowerCase().indexOf(
            this.searchValuex.toLowerCase()
          ) != -1
        );
      });

    },
    filter_district_list () {
      if (!this.search_district) {
        return this.district_list;
      }
      return this.district_list.filter((v) => {
        return v.LocationName.indexOf(this.search_district) != -1;
      });
    },
    // 酒店目的地
    hotelDestination () {
      return this.cities[0].CityNameChinese + `(${this.cities[0].CityNameEnglish})`
    },
    // 区域设置
    // hotelRegionalPosition () {
    //   return 
    // }
  },
  methods: {
    // upload () {
    //   this.$toast.loading({
    //     message: "Loading...",
    //     duration: 0,
    //     forbidClick: true,
    //     // loadingType: "spinner",
    //   });
    // },
    set_address_for_hotel_getcity () {
      // this.upload();
      if (sessionStorage.getItem("hotelCity")) {
        this.citylist = JSON.parse(sessionStorage.getItem("hotelCity"));
      } else {
        axios({
          method: "get",
          // url: "../../../js/newcity2.text",
          url: "https://timas.efasco.com/wxapp/public/newcity2.json",

          dataType: "json",
        })
          .then((res) => {
            sessionStorage.setItem("hotelCity", JSON.stringify(res.data));
            this.citylist = res.data;
            this.hotelRefreshing = false

          })
          .catch((err) => {
            console.log("失败", err);
          });
      }

      // this.citylist.map((v) => {
      //   v.value = v.CityNameChinese + "/" + v.CityNameEnglish;
      // });
      // this.$toast.clear();
    },

    get_district_list () {
      // this.upload();
      axios.get(
        "https://timas.efasco.com/HotelService.asmx/getElongLocationByCityId?citycode=" +
        this.cities[0].LocIdCity
      ).then(res => {
        // console.log('res', res);
        res.data = res.data.map(item => ({
          ...item,
          text: item.LocationName,
          value: item.LocationId
        }))
        this.district_list = [{ LocationName: "不限", text: '不限', value: '不限' }].concat(res.data);
      })
        .catch(error => {
          console.log(error);
        })
    },
    getcabins () {
      const cabins = this.cabins;
      const getCabins = cabins.map((v) => {
        return v.level_text;
      });

      return getCabins;
    },
    // onConfirm2() {
    //   this.date_D[this.date_n] = this.date_picker[2].format("yyyy-MM-dd");
    //   this.showPicker2 = false;
    //   if (this.date_n == 0) this.get_back_Date(this.date_picker[2]);
    // },
    onConfirmTwo (date) {
      const [start, end] = date;
      this.date_D[0] = new Date(start).format("yyyy-MM-dd");
      this.date_D[1] = new Date(end).format("yyyy-MM-dd");
      this.showPickerTwo = false;
    },
    formatter (day) {

      if (day.type === "start") {
        day.bottomInfo = "入住/Arr";
        // day.bottomInfo = "开始/Start";

      } else if (day.type === "end") {
        day.bottomInfo = "离店/Dep";
        // day.bottomInfo = "结束/End";

      }

      return day;
    },

    onConfirm3 (value) {
      this.cabin = value.selectedOptions[0].text;
      this.showPicker3 = false;
    },
    onConfirm5 (value) {
      this.district = value.selectedOptions[0];
      this.hotelRegionalPosition = value.selectedOptions[0].text;
      this.showPicker5 = false;
    },
    hotelOnRefresh () {
      this.set_address_for_hotel_getcity()
      setTimeout(() => {
        showToast('刷新成功');
        this.hotelRefreshing = false
      }, 1000);
      // this.hotelRefreshing = false
    },
    hotelSelectCity (value) {
      // 每次点击完之后刷新 区域位置
      this.hotelRegionalPosition = ''
      this.showPicker = false
      this.cities[0] = value
    },
    searchb () {
      //
      let d = this.district;
      if (
        !this.district ||
        (this.district && this.district.LocationName == "不限")
      )
        d = "";
      const search = {
        psg: this.$store.state.choose_psg.concat(
          this.$store.state.choose_psg_more
        ),
        cabin: this.cabin,
        cities: this.cities,
        date: this.date_D,
        // route_active: this.route_active,
        keyword: this.keyword,
        district: d,
      };
      let a = this.date_D[0];
      let b = this.date_D[1];
      if (!a || !b) {
        this.notice("请选择日期/Please select date!");
        return;
      }

      if (
        new Date(a.replace(/-/g, "/")).getTime() >
        new Date(b.replace(/-/g, "/")).getTime()
      ) {
        this.notice(
          "退房日期应在入住日期之后/The check-out date should be after the check-in date!"
        );
        return;
      }
      if (!this.cities[0]) {
        this.notice("请选择城市/Please select city!");
        return;
      }
      if (this.$store.state.choose_psg.length == 0) {
        this.notice("请选择入住人/Please select the occupant!");
        return;
      }

      // console.log(this.GetJsonData2(search));

      sessionStorage.setItem("search_hotelx", JSON.stringify(search));

      // console.log(search);
      sessionStorage.setItem("search_hotel", JSON.stringify(this.GetJsonData2(search))
      );
      this.$store.commit("removehotellist");
      console.log('search', search);
      this.$router.push({ path: "/hotellist" });
    },
    GetJsonData2 (info, pl, sortType) {

      //接口提交data参数
      //var els_L = info.level;
      let payway_hotelds2 = "All";
      // if (CltId == 1430) {
      //   payway_hotelds2 = "SelfPay";
      // }
      // console.log("sortType", sortType);

      return {
        psg: this.$store.state.choose_psg.concat(
          this.$store.state.choose_psg_more
        ),
        cityId: info.cities[0].LocIdCity,
        arrivalDate: info.date[0],
        departureDate: info.date[1],
        queryText: info.keyword,
        highRate: 9999,
        lowRate: 0,
        PaymentType: "All",
        Sort: sortType && sortType.Sort ? sortType.Sort : "Default",
        DistrictId:
          info.district && info.district.LocationId
            ? info.district.LocationId
            : payway_hotelds2,
        starRate: this.getLevel(this.cabin),
        pageIndex: 1,
        PageSize: 10,
        ResultType: "1,2,4",
        // rankType: 3,
        city: info.cities[0],
        catchN: 0,
      };
    },
    getLevel (level) {
      if (level == "不限/NoLimit") {
        return "-1";
      }
      if (level == "经济/客栈/≤2*") {
        return "0,1,2";
      }
      if (level == "三星/舒适/3") {
        return "3";
      }
      if (level == "四星/高档/4") {
        return "4";
      }
      if (level == "五星/豪华/5") {
        return "5";
      }
    },
    notice (mes) {
      this.$dialog({
        title: "提示/Notice",
        message: mes,
        theme: "round-button",
        confirmButtonText: "确定/Confirm", //改变确认按钮上显示的文字
        confirmButtonColor: "linear-gradient(160deg, #0274c9 0%, #044db5 100%)", //确定按钮文字
      });
    },
    get_back_Date (date) {
      console.log(date);
      var t = date.getTime() + 24 * 3600 * 1000;
      this.date_D[1] = new Date(t).format("yyyy-MM-dd");
    },
    cityclick (v) {
      // console.log(v, this.cities, this.cities_n);
      this.showPicker = false;
      this.district = null;
      this.cities[this.cities_n] = v;
      this.get_district_list();
    },
  },
  components: {
    psd_cont,
    infinite,
  },
};
</script>
<style lang="scss" scoped>
@import url("../../assets/css/transfer.css");
.page {
  .page_cont {
    .routertype {
      .search-cont {
        .cont {
          .hotelKeyword {
            width: 100%;
            .van-field {
              width: 100%;
            }
          }
        }
      }
    }
  }
  .pup {
    .van_listp {
      // position: relative;
      .van-search {
        position: sticky;
        width: 100%;
        top: -2px;
        background-color: white;
        z-index: 1;
      }
      .van-pull-refresh {
        .van-pull-refresh__track {
          div {
            width: 90%;
            text-align: center;
            margin: 10px auto;
            padding: 0 10px 13px;
            box-sizing: border-box;
            color: #000;
            border-bottom: 1px solid #ebedf0;
          }
        }
      }
    }
  }
}
</style>